export const productsFetch =(list)=>{
return{  type:'PRODUCTS_FETCH',
  payload: list
}}

export  const selectCompare =(id)=>{
return  {type:'SELECT_COMPARE',
  payload: id}
}

export  const Compare =(id)=>{
  return {type:'DESELECT_COMPARE',
  payload: id}
}

export  const cartAdd =(id)=>{
return { type:'Add',
  payload:id}
}

export  const cartDel =(id)=>{
return { type:'Delete',
  payload:id}
}