import React from 'react';
import {Link} from 'react-router-dom';

import Cart from './cart';
function Navs(props){
  return (<nav className="navbar navbar-light bg-success  navbar-fixed-top">
  <div className="container">
   <div className="navbar-header">
      <button type="button" className="navbar-toggle collapsed" data-toggle="collapse" data-target="#bs-example-navbar-collapse-1" aria-expanded="true">
        <span className="sr-only">Toggle navigation</span>
        <span className="icon-bar"></span>
        <span className="icon-bar"></span>
        <span className="icon-bar"></span>
      </button>
      <Link className="navbar-brand" to="/">E-Commerce App</Link>
    </div>
    <div className="collapse navbar-collapse" id="bs-example-navbar-collapse-1">
      <ul className="nav navbar-nav">
        <li className="active"><Link to="/" > Home</Link></li>
        <li> <Link to="/compare" > Compare</Link></li>
       </ul>
       <ul className="nav navbar-nav navbar-right">
        <li >
          <Link to="/cart" > Cart</Link>
          <div className="dropdown-menu">
              <Cart></Cart>
          </div>
        </li>
        <li><Link to="/login" > Login</Link></li>        
      </ul>
      </div>
  </div>
</nav>);

}

export default Navs;