import {ProductItems} from '../lists';
import {combineReducers} from 'redux';

const ProductSFetch =(list=ProductItems,action)=>{
   if(action.type==="PRODUCTS_FETCH"){
     return list;
   }    
   return list;
}

const ComparisonProduct =(selected=[],action)=>{
  
  if(action.type==="SELECT_COMPARE") { 
      if(selected.length>1){
        alert("More than two comparison not allowed");
        return selected;
      }
    let selectedItem=ProductItems.filter(e=>{return e.id==action.payload});    
    return [...selected,selectedItem[0]];
  }
  if(action.type==="DESELECT_COMPARE") {   
    console.log(action);
    let selectedItem=selected.filter(e=>{return e.id!==action.payload});    
    return selectedItem;
  }
  return selected;
}

const cartStore =(cart=[],action)=>{
  switch(action.type){
    case 'Add':
    //console.log(action.payload);
      let OldCart= cart.find((list)=>{ return list.id===action.payload});       //console.log("sds",OldCart);
      if(OldCart!==undefined){
        let newQty={...OldCart,qty:OldCart.qty+1};
        let newValue=cart.map((el)=>{return el.id===OldCart.id?newQty:el});
       // console.log("sdss",newValue);
        return newValue;
      } else {
        let selectedItem=ProductItems.find(e=>{return e.id==action.payload});
        return [...cart,selectedItem];
      }

    case 'Delete':
    console.log("deleter",action.payload);
      let val= cart.filter((el)=>{return el.id!==action.payload}) ;
      return val; 
    default:
      return cart;
  }
}

export default combineReducers({
  ProductSFetch,ComparisonProduct,cartStore});