import React,{Component} from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import  './compares.css';

class Compares extends Component{
  render(cartAction){
 
    let items=this.props.ComparisonProduct[0];
    let item=this.props.ComparisonProduct[1];
       console.log("compare",items ===undefined)
    return (
      <div className="row ">
     {items ===undefined && item ===undefined ? 
     <div class="alert alert-danger" role="alert">
  <span class="glyphicon glyphicon-exclamation-sign" aria-hidden="true"> </span> 
  <span class="sr-only">Error: </span>
   No Mobile is selected
</div>
     :null        
      }
      {items &&
          <div className="col-sm-5 col-sm-offset-1 col-md-4 col-md-offset-2 thumbnail">
      <img style={{height:"200px"}} src={items.Avatar} alt={items.Name}/>
      <div className="caption">
        <h3 >{items.Name} <small>{items.Brand}</small></h3>
        <p className="text-success">Rs. {items.Price}</p>
        <hr/>
        
          <ul className="list-group">
            <li className="list-group-item" >{items.id}</li>
            <li className="list-group-item">{items.Price}</li>
            <li className="list-group-item">{items.Color}</li>
            <li className="list-group-item">{items.Seller}</li>
            <li className="list-group-item">{items.desc}</li>
          </ul>
        
      </div>      
    </div>
      }
     {item &&
     <div className="col-sm-5 col-md-4  thumbnail">
     <img style={{height:"200px"}} src={item.Avatar} alt={item.Name}/>
      <div className="caption">
        <h3 >{item.Name} <small>{item.Brand}</small></h3>
        <p className="text-success">Rs. {item.Price}</p>
        <hr/>
        
          <ul className="list-group">
            <li className="list-group-item" >{item.id}</li>
            <li className="list-group-item">{item.Price}</li>
            <li className="list-group-item">{item.Color}</li>
            <li className="list-group-item">{item.Seller}</li>
            <li className="list-group-item">{item.desc}</li>
          </ul>
        
      </div>
    </div>
     }
      </div>
    )
  }
}
const mapToState= (state) =>{
  console.log(state)
  return state;
}
export default withRouter(connect(mapToState)(Compares));