import React from 'react';
import { connect } from 'react-redux';

import './product.css';
//import {ProductItems} from '../lists';
import {cartAdd} from '../action';
function Product(props){
  let items=props.list;
  console.log("product",props);
  return(
  <div className="card borderStyle">
    <img src={items.Avatar} className="card-img-top imgstyle" alt="..."/>
    <div className="card-body">
      <h5 className="card-title"><strong>{items.Name}</strong></h5>
      <p className="card-text"><b>Rs. {items.Price}</b></p>
      <p className="card-text text-success"><b>{items.Brand} | {items.Color}</b></p>
      <p class="card-text text-primary">
      <input type="checkbox" name={items.id} checked={props.checked} value={items.id} onClick={props.changeHandle}/>Add to compare</p>
      <p className="card-text"><small class="text-muted">Seller By {items.Seller}</small></p>
      </div>
    <div className="card-footer foot">
      <button className="btn btn-warning btn-lg btn-block" onClick={()=>{props.cartAdd(items.id)}}>Add to cart</button>
    </div>
  </div>
  
  );
}

export default connect(null,{cartAdd})(Product);