import React, { Component } from 'react';
import { render } from 'react-dom';
import { Provider } from 'react-redux'
import { createStore } from 'redux'
import reducers from './component/reducers';
import { BrowserRouter ,Route} from 'react-router-dom';

import './style.css';
import Products from './component/Product/products';
import Navs from './component/navs';
import Compares from './component/compare/compares';
import Cart from './component/navs/cart'
class App extends Component {
  constructor() {
    super();
    this.state = {
      name: 'React'
    };
  }

  render() {
    return (      
      <BrowserRouter basename={'/my-app'}>
        
        <div className="container">
        <Navs></Navs>
        <switch>
            <Route path="/compare" exact component={Compares}/>
            <Route path="/" exact component={Products}/>
            <Route path="/cart" exact component={Cart}/>
            <Route path="*" component={Products}/>
        </switch>
        </div>
     </BrowserRouter>
     
    );
  }
}

render(<Provider store={createStore(reducers)}><App /> </Provider>, document.getElementById('root'));
