import React from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom'

import Product from './product';
import './products.css';
import Notify from './notify'
class Products extends React.Component {

CompareProd=(event)=>{
 //this.selectedCompare=true;
 
 if(event.target.checked){
    this.props.dispatch({type:'SELECT_COMPARE',payload:event.target.value});
 } else {
     this.props.dispatch({type:'DESELECT_COMPARE',payload:event.target.value});
 }
}
render(){
  //let selectedCompare=false;
  console.log("Render",this.props)
    let showNotify=this.props.ComparisonProduct.length>0?            
                <div className="cmpFoot">
                  <Notify count={this.props.ComparisonProduct.length}></Notify>
                </div>    :null;
                     
  const items= this.props.ProductSFetch.map((list)=>{
    //console.log(list)
    let checkComp=this.props.ComparisonProduct.filter((el)=>{return el.id===list.id }) ; 
    return (<div  key={list.id} className="col-sm-4 col-md-3 marginStyle" >    
               <Product list={list} checked={checkComp.length?true:false} changeHandle={this.CompareProd}>
               </Product>  
         </div>
      );
  });
  
  return <div className="row ">    
      {items}     
      {showNotify}
  </div>
}

}

const mapStateToProps = state=>{  
  return state;
}
export default withRouter(connect(mapStateToProps)(Products));